.icon {
	position: relative;
  height: rem(20);
  width: rem(20);

  &.polaroid {
    width: rem(91);
    height: rem(91);

    @include media(md) {
      width: rem(150);
    }
  }

  &.polaroid-black {
    width: rem(91);
    height: rem(91);

    @include media(sm) {
      width: rem(113);
      height: rem(100);
    }
  }

  &.xs {
    width: rem(6.5);
    height: rem(6.5);
  }

  &.sm {
    width: rem(12);
    height: rem(12);
  }

  &.md {
    width: rem(15);
    height: rem(15);
  }

  &.search {
    margin-top: 0;
  }

  &.arrow-slider {
    height: rem(26);
    width: rem(180);
  }

  &.arrow-filter {
    height: rem(30);
    width: rem(9);
  }

  &.logo {
    height: rem(33);
    width: rem(171);

    &.error {
      width: rem(219);
      height: rem(46);

      @include media(md) {
        width: rem(404);
        height: rem(86);
      }
    }
  }

  &.big {
    @include media(md) {
      height: rem(30);
      width: rem(30);
    }
  }

  &.arrow-search {
    width: rem(30);
    height: rem(11);
  }

  &.quote {
    width: rem(40);
    height: rem(32);

    @include media(sm) {
      width: rem(45);
      height: rem(35);
    }

    @include media(md) {
      width: rem(61);
      height: rem(49);
    }
  }

  &.quote.big {
    width: rem(40);
    height: rem(32);

    @include media(sm) {
      width: rem(45);
      height: rem(35);
    }

    @include media(md) {
      width: rem(69);
      height: rem(56);
    }
  }

  &.heart-look {
    width: rem(14);
    height: rem(14);
  }

  &.grey {
    fill: $color-silver;
    color: $color-silver;
  }

  &.phone {
    width: rem(14);
    height: rem(14);
  }
}
