.text-transform-none {
	text-transform: none !important;
}

.overflow-auto {
	overflow: auto !important;
}

.text-decoration-underline {
	text-decoration: underline;
}

// margin
.mb, .my {
  @for $i from 1 through 20 {
    @for $i from 0 through 1 {
      &-#{$i} {
        margin-bottom: rem(4 * $i) !important;
      }
    }

    &-#{$i + 1} {
      margin-bottom: rem(16 * $i * .5) !important;
    }

    &-0 {
      margin-bottom: 0 !important;
    }
  }

  @include media(sm) {
    @for $i from 0 through 1 {
      &-sm-#{$i} {
        margin-bottom: rem(4 * $i) !important;
      }
    }

    @for $i from 1 through 20 {
      &-sm-#{$i + 1} {
        margin-bottom: rem(16 * $i * .5) !important;
      }
    }

    &-sm-0 {
      margin-bottom: 0 !important;
    }
  }

  @include media(md) {
    @for $i from 0 through 1 {
      &-md-#{$i} {
        margin-bottom: rem(4 * $i) !important;
      }
    }

    @for $i from 1 through 20 {
      &-md-#{$i + 1} {
        margin-bottom: rem(16 * $i * .5) !important;
      }
    }

    &-md-0 {
      margin-bottom: 0 !important;
    }
  }

  @include media(lg) {
    @for $i from 0 through 1 {
      &-lg-#{$i} {
        margin-bottom: rem(4 * $i) !important;
      }
    }

    @for $i from 1 through 20 {
      &-lg-#{$i + 1} {
        margin-bottom: rem(16 * $i * .5) !important;
      }
    }

    &-lg-0 {
      margin-bottom: 0 !important;
    }
  }

  @include media(xl) {
    @for $i from 0 through 1 {
      &-xl-#{$i} {
        margin-bottom: rem(4 * $i) !important;
      }
    }

    @for $i from 1 through 20 {
      &-xl-#{$i + 1} {
        margin-bottom: rem(16 * $i * .5) !important;
      }
    }

    &-xl-0 {
      margin-bottom: 0 !important;
    }
  }
}

