/* color base */
$color-black:            #000000;
$color-mine-shaft:       #333333;
$color-dove-gray:        #666666;
$color-dusty-gray:       #999999;
$color-silver:           #cccccc;
$color-cararra2:         #eceae6;
$color-mercury:          #E5E5E5;
$color-mercury2:         #E8E8E8;
$color-gallery:          #eaeaea;
$color-wild-sand:        #f5f5f5;
$color-cararra:          #ebeae6;
$color-spindle:          #C0D7E9;
$color-mona-lisa:        #FF9494;
$color-sunglo:           #E57373;
$color-monza:            #CF0A2C;
$color-cararra:          #ECEBE6;
$color-alabaster:        #F8F8F8;
$color-white:            #ffffff;

$color-transparent:      transparent;

$themeColor: 'base';

/* fonts */
$font-cormorant: 'CormorantInfant', serif;
$font-helvetica: 'HelveticaNeueCyr', serif;

$font-base: $font-helvetica;
$font-base-size: 13px;
/*$font-base-lineheight: 22px;*/

$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

/* headings */
$headings-margin-bottom:      0 !default;
$headings-font-family:        $font-cormorant !default;
$headings-font-weight:        $font-weight-light !default;
$headings-line-height:        1.2 !default;
$headings-color:              inherit !default;

// list
$margin-bottom-list-base: rem(24);
$padding-left-list-base: rem(20);
$margin-top-list-item: 0px;
$bg-point-base: $color-black;
$size-point: 6px;

// radio
$radio-size: rem(20);
$radio-border-width: 1px;
$radio-border-color: $color-black;
$radio-indent: 4px;

// checkbox
$checkbox-size: rem(20);
$checkbox-border-width: 1px;
$checkbox-border-color: $color-black;
$checkbox-indent: 4px;

// transition
$transition: all 0.25s ease-in-out;

// counter
$counter-btn-size: 50px;

// hamburger
$hamburger-size: 32px;
$hamburger-line-height: 1px;
$hamburger-line-width: 13px;
$hamburger-line-color: $color-black;
$hamburger-space: 5px;
$hamburger-rotation: 45deg;
$hamburger-translateY: $hamburger-space;
$hamburger-translateX: -50%;
$hamburger-transition: .3s ease-in-out;


/* grid */
$col: 8.333333333333333%;
$container: 1440px;
$grid-gutter-width: 16px;

$grid-breakpoints: (
    xs: 0,
    sm: 768px,
    md: 1024px,
    lg: 1290px,
    xl: 1600px
) !default;

$container-max-widths: (
    xs: 739px,
    sm: 740px,
    md: 970px,
    lg: 1260px,
    xl: 1261px
) !default;
