:root {
  --vh: 9.37px;
  --initial-vh: 9.37px;
  --inside-header-height: 60px;
  --inside-chatpane-offset-x: 0px;
  --inside-chatpane-width: 375px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

html {
	&.error {
		height: 100%;
	}
}

body {
	background: $color-white;
	font-family: $font-base;
	font-size: $font-base-size;
	font-weight: $font-weight-light;
	/*line-height: $font-base-lineheight;*/
	color: $color-black;
	width: 100%;
	margin: 0;
	padding: 0;
	position: relative;
	content-visibility: auto;
	contain-intrinsic-size: 1000px;
  -webkit-font-smoothing: antialiased;

	@include media-max('xs') {
		contain-intrinsic-size: 500px;
	}

	&.loaded {
		content-visibility: visible;
	}

  &.overflow {
    overflow: hidden;
  }

	&.error {
		height: 100%;
	}
}

.page {
	min-width: rem(320);
	width: 100%;
}

.page.dark {
	background: $color-black;
	color: $color-white;
}

.page.dark ~ .up {
	color: $color-black;
	background-color: rgba(255, 255, 255, 0.8);
}

p {
	margin: 0;
}

a {
	transition: $transition;

	&:hover {
		text-decoration: none;
	}
}

ul,
ol {
	list-style: none;
	padding: 0;
	margin: 0;
	/*position: relative;*/

	& li {
		/*position: relative;*/
		padding: 0;
		margin: 0;
	}
}

img,
svg {
	max-width: 100%;
}

img {
	height: auto;
	width: auto;
}

table {
	border-collapse: collapse;
	table-layout: fixed;
	width: 100%;
}

button {
	outline: none;
	border: 0;
	padding: 0;
	margin: 0;
	outline: none;
	background: transparent;
	color: inherit;
	font-weight: inherit;

	& i {
		margin-right: rem(5);
	}
}

select {
  border-bottom: 1px solid $color-black;
  display: inline-block;
  font: inherit;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: 0;

  &:-moz-focusring {
      color: transparent;
      text-shadow: 0 0 0 $color-black;
  }

  &.minimal {
      background-image: none;
  }
}

a,
button,
h2,
input,
textarea,
select {
  outline: none;
  box-shadow: none;

  .form-group & {
      outline: none;
      box-shadow: none;
  }

  &:focus {
      outline: none;
      box-shadow: none;

      .form-group & {
          outline: none;
          box-shadow: none;
      }
  }
}

input {
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	border-radius: 0;
	-webkit-box-shadow: none;
	box-shadow: none;
	border-width: 1px;
}

.btn:focus,
.btn.focus {
  box-shadow: none;
}

.picture,
picture {
  display: block;
  width: 100%;

  & img {
    width: 100%;
  }
}

.initial-vh {
  height: 100vh;
  height: calc(var(--app-vh,1vh) * 100);

  @include media-max(md) {
    height: calc(var(--app-vh, 1vh) * 100 - 40px);
  }

  & img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}
