h1,
.h1 {
  @extend %h1;
}

h2,
.h2 {
  @extend %h2;
}

h3,
.h3 {
  @extend %h3;
}

h4,
.h4 {
  @extend %h4;
}

h5,
.h5 {
  @extend %h5;
}

h6,
.h6 {
  @extend %h6;
}

h7,
.h7 {
  display: block;
  @extend %h7;
}

// small,
// .small {
//   @extend %smallText;
// }

.font-lg {
  @extend %font-lg;
}

.font-md {
  @extend %font-md;
}

.font-regular {
  font-weight: $font-weight-normal;
}

.font-medium {
  font-weight: $font-weight-medium;
}

p {
  margin: 0;
}

p:not(:last-child) {
  margin-bottom: rem(20);
}

a {
  color: inherit;
  text-decoration: none;

  /*&:not([class]) {
    text-decoration: underline;
  }*/

  &:not([class]):hover,
  &:not([class]).hover {
    color: $color-silver;
    text-decoration: none;
  }
}

a[href^='tel:'] ,
a[href^='mailto:']{
  white-space: nowrap;
}

b, strong, .bold {
  font-weight: $font-weight-medium;
}

.semibold {
  font-weight: $font-weight-semibold;
}

img,
picture {
  vertical-align: top;
}

button {
  cursor: pointer;
}

.color {
  &-black {
    color: $color-black;
  }

  &-mine-shaft {
    color: $color-mine-shaft;
  }

  &-dove-gray {
    color: $color-dove-gray;
  }

  &-dusty-gray {
    color: $color-dusty-gray;
  }

  &-silver {
    color: $color-silver;
  }

  &-mercury {
    color: $color-mercury;
  }

  &-gallery {
    color: $color-gallery;
  }

  &-wild-sand {
    color: $color-wild-sand;
  }

  &-cararra {
    color: $color-cararra;
  }

  &-spindle {
    color: $color-spindle;
  }

  &-mona-lisa {
    color: $color-mona-lisa;
  }

  &-white {
    color: $color-white;
  }

  &-monza {
    color: $color-monza;
  }
}

.bg {
  &-black {
    background: $color-black;
  }

  &-mine-shaft {
    background: $color-mine-shaft;
  }

  &-dove-gray {
    background: $color-dove-gray;
  }

  &-dusty-gray {
    background: $color-dusty-gray;
  }

  &-silver {
    background: $color-silver;
  }

  &-mercury {
    background: $color-mercury;
  }

  &-gallery {
    background: $color-gallery;
  }

  &-wild-sand {
    background: $color-wild-sand;
  }

  &-cararra {
    background: $color-cararra;
  }

  &-spindle {
    background: $color-spindle;
  }

  &-mona-lisa {
    background: $color-mona-lisa;
  }

  &-white {
    background: $color-white;
  }
}

.color-block {
  height: rem(180);
  width: rem(180);

  &.bg-white {
    border: 1px solid #E1E1E1;
  }
}

.underline {
  text-decoration: underline;
}

.uppercase {
  text-transform: uppercase;
}

.text-transform-none {
  text-transform: none;
}

.text-transform-normal {
  text-transform: none;
}
