%h1, %h2, %h3, %h4, %h5, %h6, %h7 {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;

  &:last-child {
    margin-bottom: 0;
  }
}

%h1 {
  font-size: rem(35);
  line-height: rem(28);
  letter-spacing: -0.3px;
  text-transform: uppercase;

  @include media(sm) {
    font-size: rem(50);
    line-height: rem(46);
  }

  @include media(lg) {
    font-size: rem(74);
    line-height: rem(63);
  }
}

%h2 {
  font-size: rem(30);
  line-height: rem(32);
  letter-spacing: -0.3px;
  text-transform: uppercase;

  @include media(sm) {
    font-size: rem(35);
    line-height: rem(28);
  }

  @include media(lg) {
    font-size: rem(50);
    line-height: rem(46);
  }
}

%h3 {
  font-size: rem(28);
  line-height: rem(28);
  letter-spacing: -0.3px;
  text-transform: uppercase;

  @include media(lg) {
    font-size: rem(40);
    line-height: rem(42);
  }
}

%h4 {
  font-size: rem(25);
  line-height: rem(30);
  text-transform: uppercase;

  @include media(lg) {
    font-size: rem(32);
    line-height: rem(32);
    letter-spacing: -0.3px;
  }
}

%h5 {
  font-size: rem(22);
  line-height: rem(24);
  letter-spacing: -0.3px;
  text-transform: uppercase;

  @include media(sm) {
    line-height: rem(26);
  }

  @include media(lg) {
    font-size: rem(25);
    line-height: rem(30);
  }
}

%h6 {
  font-family: $font-helvetica;
  font-size: rem(11);
  line-height: rem(14);
  letter-spacing: 0.4px;
  text-transform: uppercase;

  @include media(sm) {
    letter-spacing: 0;
  }

  @include media(lg) {
    font-size: rem(12);
    line-height: rem(16);
    letter-spacing: 0.4px;
  }
}

%h7 {
  font-family: $font-helvetica;
  font-size: rem(10);
  line-height: rem(12);
  letter-spacing: 0.4px;
  text-transform: uppercase;
}

%font-lg {
  font-family: $font-helvetica;
  font-size: rem(14);
  font-weight: $font-weight-light;
  line-height: rem(22);

  @include media(lg) {
    font-size: rem(16);
  }
}

%font-md {
  font-family: $font-helvetica;
  font-size: rem(15);
  font-weight: $font-weight-light;
  line-height: rem(20);
}

%inner {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

%scroll {
  &::-webkit-scrollbar {
    width: 4px;
    border-radius: 0;
  }

  &::-webkit-scrollbar-track {
    background: $color-silver;
    border-radius: 0;
  }

  &::-webkit-scrollbar-thumb {
    background: $color-black;
    width: 4px;
    border-radius: 0;
  }
}
