@import '../../../node_modules/bootstrap-4-grid/scss/breakpoints/mixins/breakpoints';

[class*="col-"] {
    padding-right: $grid-gutter-width/2;
    padding-left: $grid-gutter-width/2;
    position: unset;
}

.row {
  margin-right: -($grid-gutter-width/2);
  margin-left: -($grid-gutter-width/2);

  &--xs {
    margin-right: -($grid-gutter-width/4);
    margin-left: -($grid-gutter-width/4);

    & > [class*="col-"] {
      padding-right: $grid-gutter-width/4;
      padding-left: $grid-gutter-width/4;
    }
  }

  &--md {
    margin-right: rem(-20);
    margin-left: rem(-20);

    & > [class*="col-"] {
      padding-right: rem(20);
      padding-left: rem(20);
    }
  }

  &--md-xs {
    margin-right: rem(-5);
    margin-left: rem(-5);

    @include media(sm) {
      margin-right: rem(-12);
      margin-left: rem(-12);
    }

    @include media(md) {
      margin-right: rem(-20);
      margin-left: rem(-20);
    }

    & > [class*="col-"] {
      padding-right: rem(5);
      padding-left: rem(5);

      @include media(sm) {
        padding-right: rem(12);
        padding-left: rem(12);
      }

      @include media(md) {
        padding-right: rem(20);
        padding-left: rem(20);
      }
    }
  }

  &--lg {
    margin-right: rem(-25);
    margin-left: rem(-25);

    & > [class*="col-"] {
      padding-right: rem(25);
      padding-left: rem(25);
    }
  }
}
