@import url('../../node_modules/wowjs/css/libs/animate.css');

.fade {
	position: relative;
}

.mfp-zoom-in {

  &.full {
    z-index: 1046;
  }

  &:not(.mfp-modal) .mfp-content {
    height: 100%;

    @include media(sm) {
      height: unset;
    }
  }

	&.top .mfp-content {
		height: 100%;

		@include media(sm) {
			height: 100%;
		}
	}

	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.2s ease-in-out;
		transform: scale(0.8);
	}

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s ease-out;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: scale(1);
		}

		&.mfp-bg {
			opacity: 1;

      &.full {
        z-index: 1045;
      }
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: scale(0.8);
			opacity: 0;
		}

		&.mfp-bg {
			opacity: 0;
		}

	}

}

.mfp-fade {
	&.mfp-bg {
		opacity: 0;
		transition: all 0.15s ease-out;

		&.mfp-ready {
			opacity: 0.45;
		}

		&.mfp-removing {
			opacity: 0;
		}

	}

	&.mfp-wrap {
		&.mfp-content {
			opacity: 0;
			transition: all 0.15s ease-out;
		}

		&.mfp-ready {
			& .mfp-content {
				opacity: 1;
			}
		}

		&.mfp-removing {
			&.mfp-content {
				opacity: 0;
			}
		}
	}
}

.mfp-move-horizontal {

	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.3s;

		transform: translateX(-50px);
	}

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: translateX(0);
		}
		&.mfp-bg {
			opacity: 0.45;
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: translateX(50px);
			opacity: 0;
		}
		&.mfp-bg {
			opacity: 0;
		}

	}

}

.mfp-right-left {

  .mfp-content {
    height: 100%;
  }

  .mfp-container {
    padding: 0;
  }

	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.3s;

		transform: translateX(100vw);
	}

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: translateX(0);
		}
		&.mfp-bg {
			opacity: 0.45;
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: translateX(100vw);
			opacity: 0;
		}
		&.mfp-bg {
			opacity: 0;
		}

	}

}

.mfp-move-from-top {

	.mfp-content {
		vertical-align:top;
	}

	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.2s;

		transform: translateY(-100px);
	}

	&.mfp-bg {
		opacity: 0;
		transition: all 0.2s;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: translateY(0);
		}
		&.mfp-bg {
			opacity: 0.45;
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: translateY(-50px);
			opacity: 0;
		}
		&.mfp-bg {
			opacity: 0;
		}

	}

}

.mfp-3d-unfold {


	.mfp-content {
		perspective: 2000px;
	}

	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.3s ease-in-out;
		transform-style: preserve-3d;
		transform: rotateY(-60deg);
	}


	&.mfp-bg {
		opacity: 0;
		transition: all 0.5s;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: rotateY(0deg);
		}
		&.mfp-bg {
			opacity: 0.45;
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: rotateY(60deg);
			opacity: 0;
		}
		&.mfp-bg {
			opacity: 0;
		}

	}

}

.mfp-zoom-out {

/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.3s ease-in-out;
		transform: scale(1.3);
	}

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s ease-out;
	}

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: scale(1);
		}
		&.mfp-bg {
			opacity: 0.45;
		}
	}

	/* animate out */
	&.mfp-removing {

		.mfp-with-anim {
			transform: scale(1.3);
			opacity: 0;
		}
		&.mfp-bg {
			opacity: 0;
		}

	}

}
